#jGrowl {
  display: none !important;
}

.close-cross {
  position: absolute;
  width: 2.2rem;
  height: 2.2rem;
  background-color: transparent;
}
.close-cross::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 3px;
  background-color: var(--primary);
  transform: translateY(-50%) rotate(45deg);
  transition: var(--great-transition);
}
.close-cross::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 3px;
  background-color: var(--primary);
  transform: translateY(-50%) rotate(-45deg);
  transition: var(--great-transition);
}
.close-cross:hover::before, .close-cross:hover::after {
  background-color: var(--active-hover);
}

#sing-up-modal .inputfield {
