.spinner {
  width: 70px;
  height: 18px;
  text-align: center;
  z-index: 1;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: var(--active);
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: bouncedelay 1.4s infinite ease-in-out;
  animation: bouncedelay 1.4s infinite ease-in-out;
  /* Prevent first frame from flickering when animation starts */
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}
@keyframes bouncedelay {
  0%, 80%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  40% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
.form-btn-box {
  position: relative;
}
.form-btn-box::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 102, 105, 0.9);
  border-radius: 25px;
  transition: var(--great-transition);
  opacity: 0;
  visibility: hidden;
}
.form-btn-box .spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: var(--great-transition);
  opacity: 0;
  visibility: hidden;
}
.form-btn-box--expectation::after,
.form-btn-box--expectation .spinner {
  opacity: 1;
